import s from './template.scss';
import { checkEnv } from './../../utils/scan';

export const nodeMap = {
	phone: `${s.phone}`,
	verificationCode: `${s.verificationCode}`,
	antiFakeCode: `${s.antiFakeCode}`,
	scan: `${s.scan}`,
	sendVerificationCode: `${s.sendVerificationCode}`,
	submit: `${s.submit}`
};

const prefix = 'by-health-points';

export default (disabledPhone, verifyPhone) =>{
	return `<div class="${prefix}-wrap">
        <div class="${prefix}-antifakecode-item">
            <input type="tel" id="${s.antiFakeCode}" maxlength="16" placeholder="16" />
            ${(checkEnv() === 1 || checkEnv() === 2 || checkEnv() === 3) ? `<button id="${s.scan}"></button>` : ''}
        </div>
        <div class="${prefix}-phone-item">
            <input type="tel" ${disabledPhone ? 'disabled' : ''} id="${s.phone}" maxlength="11" placeholder="" />
        </div>
        ${verifyPhone ? `<div class="${prefix}-verificationcode-item">
            <input type="tel" id="${s.verificationCode}" maxlength="4" placeholder="" />
            <button id="${s.sendVerificationCode}"></button>
        </div>` : ''}
        <div class="${prefix}-submit-item">
            <button id="${s.submit}"></button>
        </div>
    </div>`;
};
